* {
  outline: none !important;
}

/*               Remove watermark               */
.MuiDataGrid-mainGridContainer > div:first-child {
  display: none;
}

.MuiDataGrid-columnsContainer {
  font-size: 13px;
  box-shadow: 0px 9px 7px -7px rgba(0, 0, 0, 0.1);
}

.MuiDataGrid-window {
  font-size: 12px;
}

.MuiDataGrid-sortIcon {
  font-size: 13px !important;
}

.MuiDataGrid-colCellTitleContainer {
  justify-content: center !important;
}

.MuiDataGrid-cell {
  padding: 0px 8px !important;
}

.MuiDataGrid-cell img {
  cursor: pointer;
}

.MuiDataGrid-colCell {
  padding: 0px 10px !important;
}

.MuiDataGrid-cellRight {
  text-align: right;
  justify-content: flex-end;
}

.MuiDataGrid-root {
  border: none !important;
}

.x-grid {
  height: 38.9rem;
  width: 100%;
  box-shadow: 0 0 20px -7px rgba(0, 0, 0, 0.2);
  background-color: white;
  border-radius: 4px;
}

.action__bar__stat {
  top: 4.7rem;
  position: absolute;
  margin: auto 1rem;
  font-family: "Roboto", sans-serif;
  font-weight: 100;
  font-size: 1.1rem;
}

.centered {
  justify-content: center;
}

.frame {
  display: flex;
  height: 100%;
  width: 100%;
}

.grid-holder {
  flex-grow: 1;
}
