.action_bar_container {
  margin-bottom: 3rem;
}

.action__bar {
  background-color: white;
  display: grid;
  border-radius: 4px;
  border-top-left-radius: 0 !important;
  border-top-right-radius: 0 !important;
}

.action__bar__stat {
  margin: auto 1rem;
  font-family: "Roboto", sans-serif;
  font-weight: 100;
  font-size: 1.1rem;
}

.action__bar__button {
  display: flex;
  justify-content: flex-end;
  margin: 0.5rem;
}
